<template>
    <div id="sdxy">

      <div class="headerTop">
                  <!--<br>-->
        <h2>{{this.$route.query.compantName}}爬虫程序数据演示</h2>

        <el-select v-model="value" placeholder="请选择查询的数据类型">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value" >
          </el-option>
        </el-select>

        <el-input v-model="input_comename" placeholder="请输入需要查询的公司名称" style="width: 550px;left: -90px;top: 98px;"></el-input>
        <el-button type="success" class="el-sercer" @click="test()"><a href="javascript:void(0)">搜索</a></el-button>
          </div>
        <!--左侧结果栏-->
        <div></div>
        <!--右侧样例-->
      <br>
      <br>
      <hr style="width: 99%; font-size: 15px; color:red; font-weight: bold"/>
      <div style="margin-left: 30px">
        <h3 style="font-size: 25px;text-align: left;transform: translate(90px,30px)">查询结果：</h3>
        <h3 style="font-size: 25px;margin-left: 30%;">样例结果展示：</h3>
      </div>
      <!--<div>-->
        <div class="casedatas">
            <pre v-for="(item,index) in casedata">
            {{index}} : {{item}}
          </pre>
        </div>
       <div class="result" v-if="resultTAG">
          <pre v-for="(item,index) in results">
           {{index}} : {{item}}
          </pre>
      </div>
      <!--</div>-->

      <div class="loading" v-if="loads">
        <p>正在查询，请稍后。。。</p>
        <div></div>
      </div>

    </div>
</template>

<script>

  import {post}from '../../axios/api'
    export default {
        name: "SpiderDem",
      data(){
          return{
              input_comename: '',
              casedata :{
                "公司名":"阿里巴巴（中国）网络技术有限公司",
                "注册资本":"512233.000000万美元",
                "实缴资本":"59690.0万美元",
                "统一社会信用代码":"91330100716105852F"
                },
              resultTAG :true,
              results:null,
              loads:false,
              urls: "",
              options:[
                {
                value: '选项0',
                label: '全部信息查询'
                },{
                  value:"选项1",
                  label:'基本信息查询'
                }
              ],
              value: '',
          }
      },
      methods:{
          async test(){
            debugger;
            let formData = new FormData();
            formData.append("company_name",this.input_comename);


            var url;

            if (this.value =="选项0" || this.value == ""){
              //根据选择的接口类型获取url
              url = this.$route.query.url[0];
              formData.append("ApiType",this.options[0]["label"]);

            }else if(this.value== '选项1'){
              url = this.$route.query.url[1];
              formData.append("ApiType",this.options[1]["label"]);
            }
            //开始向后台请求API
            this.loads = true;
            let resp = await post(url,formData);
            this.loads = false;

            //渲染数据到浏览器中
            if (resp.data.data){
              this.results = resp.data.data
            }else{
              this.results = resp.data.err_msg
            }
          },
        }


    }
</script>

<style scoped lang="scss">
  #sdxy{
    margin: 0;
    padding: 0;
    width: 100%;
    line-height: 100%;
    background-color: #ECF0F5;

  .headerTop{
    /*background-color: #16BF84;*/
    height: 25em;
    margin: 0;
    padding: 0;
    transform: translateX(0.5px);
    width: 99.9%;
    background: url("../../assets/images/bg.png") no-repeat center center;
    h2{
      transform: translate(-110px,60px);
      font-size: xx-large;
      text-shadow: 6px 8px 20px rgba(0,0,0,.1);
      color: #fff
    }
    .el-select{
      transform: translate(-90px,98px);
    }
  }

  }
  .Tips{
    text-align: left;
    height: 120px;
    background-color: #40BAF5;
    width: 99%;

    i{
      font-size:30px;
      font-weight: bold;
      color: #E0EDFB;
      margin-left: 20px;
      margin-top: 15px;
    }
    span:nth-child(2){
      margin-left: 15px;
      font-size: 25px;
      color: #FFFFFF;
      font-weight:bold;
    }
    span:nth-child(5){
      margin-left: 90px;
      font-size: 16px;
      color: #FFFFFF;
      font-weight:bold;
    }
  }
  .icon-close{
      margin-right: 30px;
      margin-top: 10px;
      float: right;
      z-index: 300;
     i{
       font-size: 15px;
        color:#FFFFFF; ;
     }
    }
  .casedatas{
    /*float: left;*/
    margin-left: 60%;
    color: #BC402C;
    font-size: 16px;
    position: absolute;
    width: 42%;
  }
  .result{
    float: left;
    margin-left: 100px;
    overflow:auto;
    height: 400px;
    color: #6364A7;
    font-size: 16px;
    line-height: 30px;
    width: 50%;
    padding: 10px;
  }
   pre{
      width:100%;
      text-align: justify;
      word-break: break-all;
      word-wrap: break-word;
      white-space: pre-wrap;
  };
  .loading{
    width: 400px;
    height: 300px;
    margin: 0 auto;
    opacity: 0.99;
    /*background-color: lawngreen;*/
    padding-top: 1px; //防止上边框和盒子中的选择图形上边框重叠引起的问题
    transform: translateX(-100px);
    div{
      border-radius: 50%;
      width:50px;
      height: 50px;
      border-top:10px solid red;
      border-left:10px solid slategrey;
      border-right: 10px solid chocolate;
      border-bottom: 10px solid navy;
      margin-left: 150px;
      margin-top: 90px;

       animation:turn 1s linear infinite;
            @keyframes turn{
      0%{-webkit-transform:rotate(0deg);}
      25%{-webkit-transform:rotate(90deg);}
      50%{-webkit-transform:rotate(180deg);}
      75%{-webkit-transform:rotate(270deg);}
      100%{-webkit-transform:rotate(360deg);}
    }
    };
    p{
    transform: translate(-200px, 200px);
    text-align: center;
    width: 500px;
    font-size: 16px;
    font-family: serif;
    }
  }

  .el-sercer{
    background-color: #00AA77;
    border-color: #00AA77;
    transform: translate(-6.5em, 7em);
    a{
      color: #fff;
    }
}

</style>



